/*===========================================
================ CHUNK: BASE/RTYPOGRAPHY
===========================================*/
@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto/Roboto-Regular.ttf");
}
@font-face {
  font-family: "odin";
  src: url("../fonts/monoton/Monoton-Regular.ttf");
}
html {
  font-size: min(1rem + 0.1764705882vw, 1.35rem);
  font-family: "Roboto", sans-serif;
}

/*===========================================
================ CHUNK: BASE/RESET
===========================================*/
* {
  box-sizing: border-box;
}

body {
  scroll-behavior: smooth;
  min-height: 100vh;
}

button, input, select, textarea {
  width: 190px;
}

p {
  max-width: 550px;
}

a,
a:active,
a:hover,
a:visited {
  text-decoration: none;
  color: #4d58f0;
}

/*===========================================
================ CHUNK: BASE/LAYOUT
===========================================*/
body {
  display: flex;
  color: #444;
}

section.side-bar {
  font-family: "odin";
  width: 30%;
  background-image: url("../media/odin.jpg");
  background-size: cover;
  background-position: center center;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
section.side-bar .title {
  width: 100%;
  padding: 20px 0;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.7);
}
section.side-bar .title p {
  margin: 20px 0 0 0;
  font-weight: bolder;
  font-size: 2.9rem;
  color: white;
}
section.side-bar img {
  filter: invert(100%);
  width: 70px;
}

section.main {
  width: 70%;
  padding: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
section.main > p {
  margin: 0 auto 60px auto;
  line-height: 30px;
}
section.main form {
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 0 100px rgba(100, 100, 100, 0.2);
}

input:not(:placeholder-shown):not(:focus):invalid {
  border: 1px solid red;
}

input:valid {
  border: 1px solid green;
}

fieldset {
  position: relative;
  width: 400px;
  padding: 20px;
}

legend {
  position: absolute;
  bottom: 0;
  right: 0;
  padding: 3px 6px;
  background-color: #4d58f0;
}

form {
  width: 500px;
  margin: 0 auto 30px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
form label:nth-of-type(n+3) {
  margin-top: 25px;
}
form label {
  font-weight: light;
  font-size: 0.65rem;
  letter-spacing: 1px;
  color: #555;
  text-transform: uppercase;
  display: relative;
  transform: scale(1);
}
form label.error::before {
  content: "passwords don't match";
  position: absolute;
  top: calc(100% + 5px);
  left: 0;
  color: red;
  font-size: 0.6rem;
  padding-left: 5px;
}
form input {
  font-size: 0.9rem;
  padding: 5px 10px;
  display: block;
  margin-top: 3px;
  border: 1px solid #4d58f0;
  border-radius: 5px;
}
form > button {
  margin-top: 30px;
  background: #4d58f0;
  color: white;
  padding: 5px 0;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}